module.exports = "import React from 'react';\nimport PropTypes from 'prop-types';\nimport {withStyles} from '@material-ui/core/styles';\nimport Button from '@material-ui/core/Button';\nimport Tooltip from '@material-ui/core/Tooltip';\n\nconst styles = theme => ({\n    lightTooltip: {\n        background: theme.palette.common.white,\n        color     : theme.palette.text.primary,\n        boxShadow : theme.shadows[1],\n        fontSize  : 11\n    },\n    arrowPopper : {\n        '&[x-placement*=\"bottom\"] $arrowArrow': {\n            top        : 0,\n            left       : 0,\n            marginTop  : '-0.9em',\n            width      : '3em',\n            height     : '1em',\n            '&::before': {\n                borderWidth: '0 1em 1em 1em',\n                borderColor: `transparent transparent ${theme.palette.grey[700]} transparent`\n            }\n        },\n        '&[x-placement*=\"top\"] $arrowArrow'   : {\n            bottom      : 0,\n            left        : 0,\n            marginBottom: '-0.9em',\n            width       : '3em',\n            height      : '1em',\n            '&::before' : {\n                borderWidth: '1em 1em 0 1em',\n                borderColor: `${theme.palette.grey[700]} transparent transparent transparent`\n            }\n        },\n        '&[x-placement*=\"right\"] $arrowArrow' : {\n            left       : 0,\n            marginLeft : '-0.9em',\n            height     : '3em',\n            width      : '1em',\n            '&::before': {\n                borderWidth: '1em 1em 1em 0',\n                borderColor: `transparent ${theme.palette.grey[700]} transparent transparent`\n            }\n        },\n        '&[x-placement*=\"left\"] $arrowArrow'  : {\n            right      : 0,\n            marginRight: '-0.9em',\n            height     : '3em',\n            width      : '1em',\n            '&::before': {\n                borderWidth: '1em 0 1em 1em',\n                borderColor: `transparent transparent transparent ${theme.palette.grey[700]}`\n            }\n        }\n    },\n    arrowArrow  : {\n        position   : 'absolute',\n        fontSize   : 7,\n        width      : '3em',\n        height     : '3em',\n        '&::before': {\n            content    : '\"\"',\n            margin     : 'auto',\n            display    : 'block',\n            width      : 0,\n            height     : 0,\n            borderStyle: 'solid'\n        }\n    },\n    button      : {\n        margin: theme.spacing.unit\n    }\n});\n\nclass CustomizedTooltips extends React.Component {\n    state = {\n        arrowRef: null\n    };\n\n    handleArrowRef = node => {\n        this.setState({\n            arrowRef: node\n        });\n    };\n\n    render()\n    {\n        const {classes} = this.props;\n\n        return (\n            <div>\n                <Tooltip title=\"Add\">\n                    <Button className={classes.button}>Default</Button>\n                </Tooltip>\n                <Tooltip title=\"Add\" classes={{tooltip: classes.lightTooltip}}>\n                    <Button className={classes.button}>Light</Button>\n                </Tooltip>\n                <Tooltip\n                    title={\n                        <React.Fragment>\n                            Add\n                            <span className={classes.arrowArrow} ref={this.handleArrowRef}/>\n                        </React.Fragment>\n                    }\n                    classes={{popper: classes.arrowPopper}}\n                    PopperProps={{\n                        popperOptions: {\n                            modifiers: {\n                                arrow: {\n                                    enabled: Boolean(this.state.arrowRef),\n                                    element: this.state.arrowRef\n                                }\n                            }\n                        }\n                    }}\n                >\n                    <Button className={classes.button}>Arrow</Button>\n                </Tooltip>\n            </div>\n        );\n    }\n}\n\nCustomizedTooltips.propTypes = {\n    classes: PropTypes.object.isRequired\n};\n\nexport default withStyles(styles)(CustomizedTooltips);\n"